:root {
    --background-color: #002248;
    --body-background: #24A9BD;
}

@keyframes site {
    0% {opacity: 0;}
    100% { opacity: 1;}
};

.site {
    display: none;
    background-image: linear-gradient(white, var(--background-color));
    opacity: 0;
    animation: site 2s forwards;
    animation-delay: 5.0s;
}

@keyframes welcome-disappear {
    0% { opacity: 1;}
    100% { opacity: 0;}
}

.welcome {
    background-color: var(--background-color);
    min-height: 100vh;
    height: 100%;
    max-width: 100vw;
    font-size: 100%;
    animation: welcome-disappear 1.5s ease-in-out forwards;
    animation-delay: 3.5s;

}

@keyframes welcome {
    0% { opacity: 0;}
    50% { opacity: 0;}
    100% { opacity: 1;}
}

.welcome-header {
    animation: welcome 2s ease-in-out forwards;
}


#back-container {
    display: flex;
    justify-content: flex-end;
}

.map {
    max-width: 600px;
    max-height: 450px;
    width: 80vw;
}

.wrapped-menu {
    display: flex;
    flex-direction: column;
    background-color: var(--background-color);
    color: white;
    align-items: center;
    width: 100vw;
    position: absolute;
    top:-100px;
    z-index:1;
    transition: transform 1s;
}

.mobile-nav {
    justify-content: space-between;
}

.menu-wrapper {
    background-color: transparent;
    color: white;
    border: none;
    display: flex;
    flex-direction: column;
    margin: 7px;
    z-index: 3;
}

.hamburger-line {
    width: 30px;
    height: 4px;
    background-color: white;
    margin: 4px 10px;
    transition: transform 1s;
}



@media screen and (max-width: 320px) {
    html {
        font-size: 5px;
    }
    .mobile-nav {
        display: flex!important;
    }

    .desktop-nav {display: none!important;}
}

@media screen and (min-width: 320px) and (max-width: 640px) {
    html {
        font-size: 8px;
    }

    .logo {
        width: 150px;
        height: auto;
    }

    .mobile-nav {
        display: flex!important;
    }
    .desktop-nav {display: none!important;}


}
@media screen and (min-width: 641px ) and (max-width: 960px) {
    html {
        font-size: 15px;
    }
    .logo {
        width: 250px;
        height: auto;
    }
    .mobile-nav {
        display: none;
    }
}

@media screen and (min-width: 961px) {
    html {
        font-size: 17px;
    }
    .mobile-nav {
        display: none;
    }
}

body {
    font-size: 20px;
    font-family: 'Source Sans Pro', 'sans-serif';
    background-image: linear-gradient(white, var(--background-color));
}

.stitch-container {
    font-size: 100vw;
}

.stitch {
    font-size: 0.14em;
    margin-right: 0.055em;
    color: white;
}

* {
    padding: 0px;
    margin: 0px;
    scroll-behavior: smooth;
}

.link {
    text-decoration: none;
    padding: 5px;
    color: white;
    font-size: 2rem;
    z-index: 3;
}

.link:hover {
    color: yellow;
    transition: color 1.5s;
}
.flex {
    display: flex;
}

.center {
    justify-content: center;
    align-items: center;
}

.wrap {
    flex-wrap: wrap;
}

.column {
    flex-direction: column
}

.between {
    justify-content: space-between;
}

.baseline {
    align-items: flex-start;
}

.margin-top {
    margin-top: 5rem;
}

.nav {
    background-color: var(--background-color);
    z-index: 3;
}

.section {
    min-height: 100vh;
    position: relative;
}

.footer {
    background-color: var(--background-color);
}

.icon {
    font-size: 2em;
    color: white;
    padding: 5px;
}

.sign {
    color: white;
    font-size: 1rem;
}

.photo {
    background-repeat: no-repeat;
    background-attachment: fixed;
    min-height: 300px;
    height: 50vh;
    background-size: cover;
}

.offer {
    background-image: url('../assets/offer.jpg');
}

.about {
    background-image: url('../assets/about.jpg')
}

.contact {
    background-image: url('../assets/contact.jpg');
}

.photo-header {
    background-color: var(--background-color);
    height: 100%;
    width:100vw;
    font-size: 10rem;
    color: white;
    padding: 0px;
    margin: 0px;
}

.mask {
    background-color: var(--background-color);
    height: 100%;
    width: 100%;
    opacity: 0.5;
}

.content {
    padding: 5%;
    height: 100%;
    margin-bottom: 5%;
}

.offer-text {
    align-self: center;
}

@keyframes underline {
    0% {text-decoration: none}
    100% {text-decoration: underline}
}
.offer-text:hover {
    text-decoration: underline;
    animation: underline 2s ease-in-out;
}

.offer-image {
    width: 100%;
    max-width: 400px;
}

.offer-section {
    display: grid;
    grid-template-columns: 50% 50%;
    border-bottom: 1px solid var(--background-color);
    margin-bottom: 3rem;
    margin: 0 10%;
}

.button {
    margin: 10px;
    background-color: transparent;
    color: var(--background-color);
    border: 2px solid var(--background-color);
    padding: 10px;
    font-weight: bold;
    width: 150px;
    height: 50px;
}


.button:hover {
    background-image: none;
    background-color: var(--background-color);
    color: yellow;
    transition: 1s;
    cursor: pointer;
}

.about-section {
    width: 300px;
    height: 50vh;
    align-items: center;
    margin-bottom: 20px;
    margin-right: 60px;
    margin-left: 60px;

}

.about-section>* {
    padding-top: 20px;
}

.about-header {
    width: 100vw;
    text-align: center;
}



.paragraph {
    text-align: justify;
    border-radius: 10px 10px;
    padding: 10px;
    color: white;
    margin-bottom: 20px;

}

.about-icon {
    color: yellow;
    font-size: 3rem;
}

.section-text {
    color: yellow;
    font-size: 3rem;
    padding-left: 20px;
}



